*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

@media screen and (min-width: 320px) {
  .container {
    width: 320px;
    outline: 1px solid tomato;
    margin-left: auto;
    margin-right: auto;
    padding-left: 20px;
    padding-right: 20px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    outline: 1px solid tomato;
    margin-left: auto;
    margin-right: auto;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    width: 1280px;
    outline: 1px solid tomato;
    margin-left: auto;
    margin-right: auto;
    padding-left: 32px;
    padding-right: 32px;
  }
}

p, h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

address {
  font-style: normal;
}

button {
  cursor: pointer;
  font-family: inherit;
  display: block;
}

.list {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

.link {
  text-decoration: none;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

@media screen and (max-width: 767px) {
  .main_section {
    padding-top: 20px;
    padding-bottom: 40px;
  }
}

@media screen and (min-width: 768px) {
  .main_section {
    padding-top: 60px;
    padding-bottom: 60px;
  }
}

.list_film {
  flex-wrap: wrap;
  display: flex;
}

@media screen and (min-width: 320px) {
  .list_film {
    gap: 20px;
  }
}

@media screen and (min-width: 768px) {
  .list_film {
    gap: 32px;
  }
}

@media screen and (min-width: 1280px) {
  .list_film {
    gap: 32px 16px;
  }
}

li {
  outline: 1px solid gray;
}

@media screen and (min-width: 1280px) {
  li {
    height: 634px;
  }
}

.list_film_item {
  min-width: 280px;
  height: 402px;
}

@media screen and (min-width: 768px) {
  .list_film_item {
    width: calc(50% - 16px);
    height: 455px;
  }
}

@media screen and (min-width: 1280px) {
  .list_film_item {
    width: calc(33.3333% - 10.6667px);
  }
}

.list_film_item:hover, .list_film_item:focus {
  transition: transform .15s, all cubic-bezier(.2, 0, .2, 1);
  transform: scale(1.05);
}

.backdrop {
  background-color: rgba(0, 0, 0, .2);
  transition-property: opacity;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.is-hidden {
  opacity: 0;
  pointer-events: none;
}

.preloader {
  width: 100%;
  height: 100%;
  z-index: 2;
  opacity: 1;
  visibility: visible;
  background-color: #000;
  transition: all 1s;
  position: fixed;
  top: 0;
  left: 0;
}

.preloader .loader {
  width: 100px;
  height: 100px;
  border: 10px solid #f3f3f3;
  border-top-color: #3498db;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.preloader.done {
  opacity: 0;
  visibility: hidden;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

